import React from 'react';
import '../Style/Apresentacao.css'

const Body = () =>
{

    return(
        <div className='Body'>
            <div className='Body--Design'>
            <h1>DESIGN DE INTERIOR</h1>
            <p>Perspectivas artísticas da edificação buscando ressaltar a arquitetura, decoração ou projeto de forma ilustrativa. As Ilustrações buscam o foto-realismo, facilitando o entendimento e aceitação da proposta.</p>
            </div>
            <div className='Card--Demais'>

            <div className='Card'>
            <h3>ARQUITETÔNICO</h3>
            <img src={'img/apresentacao/img01.jpg'} alt={''}/>
            <p>Tem como objetivo transcrever o seu sonho em um desenho técnico. Atendendo as necessidades de conforto, segurança e custo. Elaboramos o projeto arquitetônico para quaisquer fins relacionado a prefeituras (ex.: Aprovação, Regularização, etc..).</p>
            </div>

            <div className='Card'>
            <h3>ESTRUTURAL</h3>
            <img src={'img/apresentacao/img02.jpg'} alt={''}/>
            <p>Desenvolve-se os projetos que se referem ao dimensionamento das estruturas (lajes, vigas, pilares e fundação) que irão sustentar a edificação. Com objetivo de, basicamente, garantir três fatores fundamentais na construção: Segurança, Economia e Durabilidade. Temos nosso foco em estruturas de concreto armado.</p>
            </div>

            <div className='Card'>
            <h3>HIDROSSANITÁRIO</h3>
            <img src={'img/apresentacao/img03.jpg'} alt={''} />
            <p>O objetivo é o funcionamento ideal de todo o sistema hidráulico, pluvial e sanitário. Dimensionando e planejando o caminho das tubulações, afim de alcançar o melhor custo/benefício e conjuntamente reduzir eventuais problemas futuros.</p>
            </div>

            <div className='Card'>
            <h3>INSTALAÇÃO ELÉTRICA</h3>
            <img src={'img/apresentacao/img04.jpg'} alt={''}/>
            <p>O objetivo é o funcionamento ideal de todo o sistema Elétrico. Dimensionando dispositivos de seguranças e planejando o caminho dos cabos, afim de alcançar o melhor custo/benefício e conjuntamente reduzir eventuais problemas futuros. Elaboramos projetos de instalação elétrica de baixa tensão.</p>
            </div>

            </div>
        </div>
        )    
    }
    export default Body;