import React from 'react';
import '../Style/Header.css'
import { useState} from 'react';
const Header = ({exibir}) =>
{
    const [colorHeader, setColorHeader] = useState('transparent');
   
      const scrollListener = ()=>{
        if(window.scrollY>10){
          setColorHeader('rgba(255,255,255,0.93)')
        }
        else{setColorHeader('transparent')}
      }

      window.addEventListener('scroll',scrollListener);

    return(
        <div className='Header' style={{backgroundColor: colorHeader,transition:'0.5s'}}>
        <a href='./' className='Header--logo'>  
        <img src={'icons/logo.png'} alt={''}/>
        </a>
        <div className='Header--menu'>  
        <button type='button' onClick={()=>{exibir(true)}}>Inicio</button>
        <button type='button' onClick={()=>{exibir(false)}}>Projetos</button>
        </div>
        <div className='Header--contact'>  
        <a href='https://api.whatsapp.com/send?phone=553193281399'>
        <img src='icons/whatsApp.png' alt={''}/>
        </a>
        <a href='https://www.instagram.com/eixo_x_projetos'>
        <img src='icons/instagram.png' alt={''}/>
        </a>
        
        </div>
        </div>
        )    
    }
    export default Header;